type Filter = Partial<{
  root: boolean
  mainBg: boolean
  rankBg: boolean
  medal: boolean
  rankContainer: boolean
  rankLabel: boolean
  nameLabel: boolean
  scoreContainer: boolean
  scoreText: boolean
  scoreValue: boolean
}>;

class LBEntryLayout {
  public readonly root: PIXI.Container
  public readonly mainBg: PIXI.Sprite
  public readonly rankBg: PIXI.Sprite
  public readonly medal: PIXI.Sprite
  public readonly rankContainer: PIXI.Container
  public readonly rankLabel: PIXI.Text
  public readonly nameLabel: PIXI.Text
  public readonly scoreContainer: PIXI.Container
  public readonly scoreText: PIXI.Text
  public readonly scoreValue: PIXI.Text

  constructor(filter?: Filter) {
    populate(this, raw, filter);
  }
}

export { LBEntryLayout, Filter, raw };