type Filter = Partial<{
  root: boolean;
  soundToggle: boolean;
  soundLabelOff: boolean;
  soundLabelOn: boolean;
  restartButton: boolean;
  leaderboardButton: boolean;
  closeButton: boolean;
}>;

class SettingsPopupLayout {
  public readonly root: PIXI.Container;
  public readonly soundToggle: PIXI.Container;
  public readonly soundLabelOn: PIXI.Sprite;
  public readonly soundLabelOff: PIXI.Sprite;
  public readonly restartButton: PIXI.Sprite;
  public readonly leaderboardButton: PIXI.Sprite;
  public readonly closeButton: PIXI.Sprite;

  constructor(filter?: Filter) {
    populate(this, raw, filter);
  }
}

export { SettingsPopupLayout, Filter, raw };