import { BubbleGameBoard } from "../BubbleGameBoard";
import { popOutBubble } from "../bubbles/behavior/popOutBubble";

export function popOutLines(board: BubbleGameBoard, upToRow: number): void {
  for (const bubble of Object.values(board.getData().grid.values)) {
    if (bubble.row > upToRow) {
      popOutBubble(board, bubble);
    }
  }
}