import { BubbleColor, Bubble } from 'js/bubbleCore/bubbles/types';

const colorMap: {[key in BubbleColor]: number} = {
  [BubbleColor.BLUE]: 0x4D90F0,
  [BubbleColor.GREEN]: 0x37FB41,
  [BubbleColor.PINK]: 0xFF90EF,
  [BubbleColor.RED]: 0xFC4D30,
  [BubbleColor.TURQUOISE]: 0x40EAFA,
  [BubbleColor.YELLOW]: 0xF6F800,
  [BubbleColor.NONE]: 0xffffff,
}

export function getColorForBubble(bubble: Bubble): number {
  return colorMap[bubble.core.color];
}