import * as PIXI from 'pixi.js';

export class Background extends PIXI.Sprite {
  constructor() {
    super(PIXI.Texture.from('BACKGROUND'));
  }

  public handleResize([width, height]: [number, number]): void {
    // stretch to fill all available space, maintaining image aspect ratio
    const xScale = width / this.texture.baseTexture.width;
    const yScale = height / this.texture.baseTexture.height;
    const maxScale = Math.max(xScale, yScale)
    this.scale.set(maxScale);
    // keep the image centered
    this.position.set((width - this.width) / 2, (height - this.height) / 2)
  }
}
