import { Graphics } from "pixi.js"

export type Particle = {
  x: number
  y: number
  r: number
  d: number
  tilt: number
  tiltAngle: number
  tiltVelocity: number
  color: number
}

const COLORS = [
  { main: 2003199, alt: 4620980 },
  { main: 7048739, alt: 5597999 },
  { main: 16766720, alt: 12433259 },
  { main: 16761035, alt: 14184595 },
  { main: 11393254, alt: 11584734 },
  { main: 15631086, alt: 14315734 },
  { main: 10025880, alt: 10145074 },
  { main: 16032864, alt: 13468991 },
  { main: 13789470, alt: 10506797 },
  { main: 14423100, alt: 11674146 }
]

export class Confetti extends PIXI.Container {
  particles: Particle[]
  angleOffset: number
  tiltAngleOffset: number
  g: Graphics
  active: boolean = false
  constructor(W: number, H: number, maxParticles: number) {
    super()

    this.particles = []
    this.angleOffset = 0
    this.tiltAngleOffset = 0

    this.g = new PIXI.Graphics();
    this.addChild(this.g);

    for (let i = 0; i < maxParticles; i++) {
      this.createParticle(W, H, maxParticles, i)
    }
  }

  activate() {
    this.angleOffset = 0
    this.tiltAngleOffset = 0
    this.active = true
  }

  createParticle(W: number, H: number, maxParticles: number, id: number) {
    this.particles.push({
      x: Math.random() * W - W/2,
      y: -Math.random() * H,
      r: 10 + 30 * Math.random(),
      d: Math.random() * maxParticles + 10,
      tilt: Math.floor(10 * Math.random()) - 10,
      tiltAngle: 0,
      tiltVelocity: 0.07 * Math.random() + 0.05,
      color: this.getColor(id, true)
    })
  }

  update() {
    if (this.active) {

      this.angleOffset += 0.01;
      this.tiltAngleOffset += 0.1;

      this.g.clear();
      for (const particle of this.particles) {
        this.updateParticle(particle, this.particles.indexOf(particle))
        this.drawParticle(particle)
      }
    }
  }

  updateParticle(particle: Particle, particleId: number) {
    particle.tiltAngle += particle.tiltVelocity;
    particle.y += (Math.cos(this.angle + particle.d) + 10 + particle.r / 2) / 2;
    particle.x += Math.sin(this.angle);
    particle.tilt = 15 * Math.sin(particle.tiltAngle - particleId / 3);
    // particle.color = 0 < particle.tilt ?
    particle.color = this.getColor(particleId, 0 < particle.tilt ? true : false);
  }

  drawParticle(particle: Particle) {
    this.g.lineStyle(particle.r / 2, particle.color);
    this.g.moveTo(particle.x + particle.tilt + particle.r / 4, particle.y);
    this.g.lineTo(particle.x + particle.tilt, particle.y + particle.tilt + particle.r/4);
  }

  getColor(particleId, main): number {
    const colorPair = COLORS[particleId % COLORS.length]
    return main ? colorPair.main : colorPair.alt
  }
}