import { BubbleGameBoard } from 'js/bubbleCore/BubbleGameBoard';
import { BubbleGameBoardView } from 'js/bubbleCore/view/BubbleGameBoardView';
import { ICoords } from 'js/bubbleCore/view/InputCatcher';
import { IPoint, getAngle } from 'framework/utils/math-utils';
import { EventType, IEvent } from 'js/bubbleCore/events';
import { swapBubbles } from './handlers/swap';
import { SignalBinding } from 'js/framework/utils/Signal';
import { showInterstitial } from 'js/sdk/showInterstitial';
import { Game } from 'js/framework/Game';

interface IControllerConfig {
  onGameOver: () => Promise<void>;
  board: BubbleGameBoard,
  boardView: BubbleGameBoardView,
  mobile: boolean;
}

export class Controller {
  private readonly game: Game;
  private readonly board: BubbleGameBoard;
  private readonly view: BubbleGameBoardView;
  private readonly gameOverBinding: SignalBinding<IEvent<EventType>>;

  constructor(game: Game, config: IControllerConfig) {
    this.game = game;
    this.board = config.board;
    this.view = config.boardView;

    const inputCatcher = this.view.getInputCatcher();

    (
      config.mobile ?
      inputCatcher.signals.onPointerUp :
      inputCatcher.signals.onPointerDown
    ).add((coords: ICoords) => {
      this.handlePointerDown(coords);
    });


    this.gameOverBinding = this.board.signals.onEventProcessed.add((event) => {
      if (event.type !== EventType.DeathLineCrossed) return;
      config.onGameOver();
    });

    const sfx = this.game.sounds;

    this.view.getPauseButton().on('pointerup', async () => {
      sfx.play("button_click");
      if(this.game.isPaused()) {
        await showInterstitial();
        this.game.signals.onUnpause.dispatch();
      } else {
        this.game.signals.onPause.dispatch();
      }
    });
  }

  public destroy() {
    this.gameOverBinding.detach();
    this.view.getPauseButton().removeAllListeners();
  }

  private handlePointerDown(coords: ICoords): void {
    if (!this.isGameOver() && !this.game.isPaused()) {
      console.log("Game not paused");
      if (this.canShoot(coords.hexSpace)) {
        console.log("can shoot")
        this.board.shoot(coords.hexSpace);
      } else {
        console.log("can't shoot")
        swapBubbles(this.board.getData().shooter, this.board.getEventsQueue())
      }
    }
  }

  private isGameOver(): boolean {
    return this.board.getData().state.gameOver
  }

  private canShoot(target: IPoint): boolean {
    const { position, angleLimitRad } = this.board.getData().shooter;
    const shootAngle = Math.abs(getAngle(position, target) + Math.PI * 0.5);
    return shootAngle <= angleLimitRad;
  }

  public update(): void {
    const coords = this.view.getInputCatcher().getCoords();
    if (!this.isGameOver() && this.canShoot(coords.hexSpace)) {
      this.view.displayTestShoot(this.board.testShoot(coords.hexSpace));
    }
  }
}