import * as PIXI from 'pixi.js';
import { Lerper } from 'framework/utils/Lerper';
import { Range } from 'framework/utils/Range';
import { waitForMs } from 'framework/utils/async-utils';
import { TweenManager } from 'framework/TweenManager/TweenManager';
import { showInterstitial } from "js/sdk/showInterstitial";
import { GameOverLayout } from 'js/layouts/GameOverLayout';
import { LeaderboardPopupV2 } from './LeaderboardPopupV2';
import { game } from 'js/main';
import { Confetti } from 'js/bubbleCore/view/Confetti';

interface IGameOverPopupV2Config {
  parent: PIXI.Container;
  tweenMgr: TweenManager;
  restart: () => unknown;
  score: number;
}

export class GameOverPopupV2 {
  private readonly layout: GameOverLayout;
  private readonly lerper: Lerper;
  private readonly tweenMgr: TweenManager;

  private confetti: Confetti;
  private confettiUpdate: VoidFunction | null;

  private readonly score: number;

  constructor({
    parent,
    tweenMgr,
    score,
    restart,
  }: IGameOverPopupV2Config, newBest: boolean = false) {
    this.tweenMgr = tweenMgr;
    this.score = score;

    this.layout = new GameOverLayout();
    parent.addChild(this.layout.root);

    const {
      scorePoints,
      replayButton,
      root,
      overlay,
      confettiContainer,
      scoreLabel,
      newBestLabel,
    } = this.layout;

    overlay.texture = PIXI.Texture.WHITE;
    overlay.tint = 0;
    overlay.alpha = 204/255;
    overlay.scale.set(game.screen.width/16, game.screen.height/16);

    if (newBest) {
      newBestLabel.visible = true;
      scoreLabel.visible = false;
    }

    this.lerper = new Lerper({
      initValue: -1,
      lerp: 0.1,
      snap: 0.5,
      autoUpdate: true,
      range: new Range(0, Infinity),
      destroySignal: root.onDestroyed,
    });

    replayButton.visible = false;

    this.confetti = new Confetti(game.screen.width, game.screen.height, 100);
    confettiContainer.addChild(this.confetti);
    this.confettiUpdate = () => { this.confetti.update() }
    game.ticker.add(this.confettiUpdate)

    // if (highscore) {
    //   highscoreText.text = highscoreText.text
    //     .replace('%%%', highscore.toString());
    // } else {
    //   highscoreText.visible = false;
    // }

    const defaultScoreLabel = scorePoints.text;
    this.lerper.signals.onValueUpdate.add(async (value) => {
      const rounded = Math.round(value);
      scorePoints.text = defaultScoreLabel.replace('%%%', rounded.toString());
      if (rounded === this.score) this.showContinueButton(restart);
    });

    this.showPopup();
    // this.showLeaderboard();
  }

  private async showPopup() {

    this.tweenMgr.create(this.layout.root)
      .from({alpha: 0}, 300, this.tweenMgr.easing.Sinusoidal.Out, true);

    this.tweenMgr.create(this.layout.titleContainer)
      .from({y: -700, alpha: 0}, 300, this.tweenMgr.easing.Cubic.InOut, true, 300);

    waitForMs(450).then(() => this.confetti.activate());

    this.tweenMgr.create(this.layout.scorePoints)
      .from({alpha: 0}, 300, this.tweenMgr.easing.Cubic.In, true, 600);

    if (this.layout.newBestLabel.visible) {
      this.tweenMgr.create(this.layout.newBestLabel)
      .from({alpha: 0}, 300, this.tweenMgr.easing.Cubic.In, true, 600);
    }

    waitForMs(600).then(() => this.lerper.setTarget(this.score));
  }

  private async hidePopup() {
    game.ticker.remove(this.confettiUpdate!)
    this.confettiUpdate = null;

    await this.tweenMgr.create(this.layout.root)
      .to({alpha: 0}, 400, this.tweenMgr.easing.Sinusoidal.Out, true)
      .asyncOnComplete();


    this.layout.root.destroy();
  }

  private async showContinueButton(restart: () => unknown): Promise<void> {
    const {
      replayButton,
    } = this.layout;

    await Promise.all([
      replayButton,
    ].map(el => {
      el.visible = true;
      return this.tweenMgr.create(el)
        .from({alpha: 0}, 300, this.tweenMgr.easing.Sinusoidal.Out, true)
        .asyncOnComplete();
    }));

    replayButton.once(
      'pointerdown',
      async () => {
        await showInterstitial();
        this.hidePopup();
        restart();
      },
    );
  }

}
