import { Scene } from 'framework/SceneManager/Scene';
import { Game } from 'framework/Game';
import { ScenesName } from 'js/scenes/scenes';

import { BubbleGameBoard } from '../bubbleCore/BubbleGameBoard';
import { BubbleGameBoardView } from '../bubbleCore/view/BubbleGameBoardView';

import { HexGridType } from '../bubbleCore/types';
import { Background } from 'js/scenes/common/Background';
import { ILevelData } from 'js/bubbleCore/levelParser/parseLevel';
import { Controller } from 'js/bubbleCore/Controller';

import { GameOverPopupV2 } from 'js/scenes/ui/GameOverPopupV2';
import { WaitingScreen } from 'js/scenes/ui/WaitingScreen';

import { loadUserData } from 'js/sdk/loadUserData';
import { saveUserData } from 'js/sdk/saveUserData';
import { logEvent } from 'js/sdk/logEvent';

import triggerGameStart from "js/sdk/triggerGameStart";
import triggerGameOver from "js/sdk/triggerGameOver";
import { appHeight } from 'js/main';
import { SecondChancePopup } from './ui/SecondChancePopup';
import { popOutLines } from 'js/bubbleCore/handlers/popOutLines';
import { SettingsPopup } from './ui/SettingsPopup';
import { LeaderboardPopupV2 } from './ui/LeaderboardPopupV2';
import { setPlayerEntry } from 'js/sdk/ysdk-lb';

let highscore: number;

const DEATH_LINE_Y = 21

const levelData: ILevelData = {
  config: {
    fireEvery: 10,
    availableColors: [
      {startSec: 0, colors: 3},
      {startSec: 30, colors: 4},
      {startSec: 60, colors: 5},
      {startSec: 90, colors: 6}
    ]
  },
  grid: {
    type: HexGridType.OddR,
    hexLimits: {x: 0, y: 0, width: 11, height: 28},
    asymetrical: true,
    rowsToFill: 6,
    scrollSpeed: 0.0018,
    fillScrollSpeed: 0.01,
    deathLineY: DEATH_LINE_Y,
    scrollFill: 5,
  },
  popOutSpace: {
    gravity: 0.02,
    popOutVelRange: {left: -0.4, right: 0.4, top: -0.2, bottom: 0},
    pointZones: [
      { x: 0, score: 50 },
      { x: 0.2, score: 100 },
      { x: 0.4, score: 250 },
      { x: 0.6, score: 100 },
      { x: 0.8, score: 50 },
    ],
    pointZonesY: DEATH_LINE_Y + 1,
    bouncers: {
      y: DEATH_LINE_Y + 0.05,
      width: 0.02,
      height: 0.05,
    },
  },
  shooter: {
    bubbleSpeed: 1,
    positionY: DEATH_LINE_Y + 3,
    angleLimitDeg: 85,
  },
};

export type GameState = "RegularPlay" | "SettingsPopup" | "SecondChancePopup" | "SecondChancePlay" | "GameOverPopup" | "Leaderboards"
export type StateEvent = "Pause" | "Unpause" | "Close" | "Continue" | "GameOver" | "NewBest"

export interface ILayout {
  root: PIXI.Container
}

export interface IPopup {
  layout: ILayout
  showPopup: () => void
  hidePopup: () => void
}

export class MainScene extends Scene {
  private readonly bg: Background;
  private gameBoard: BubbleGameBoard;
  private gameBoardView: BubbleGameBoardView;
  private controller: Controller;

  private autoUpdate: boolean = true;
  private gameStarted = false;

  private level: ILevelData;

  private settingsPopup: SettingsPopup | null;

  public isPaused: boolean = false;

  private popupStack: IPopup[]

  private state: GameState

  constructor(game: Game) {
    super(game);

    // @ts-ignore
    window.scene = this;

    this.bg = this.root.addChild(new Background());
    this.level = JSON.parse(JSON.stringify(levelData));
    this.gameBoard = new BubbleGameBoard();

    this.state = "RegularPlay"

    this.gameBoardView = new BubbleGameBoardView({
      gameBoard: this.gameBoard,
      hexSize: 39,
      scale: 1,
      verticalOffset: 0,
      soundManager: this.game.sounds,
      game: this.game
    });

    this.game.signals.onPause.add(() => {
      this.isPaused = true;
      if (!this.settingsPopup && !this.gameBoard.getData().state.gameOver) {
        this.showSettings();
      }
      this.gameBoardView.disableButtons();
    })

    this.game.signals.onUnpause.add(() => {
      this.isPaused = false;
      if (this.settingsPopup) {
        this.settingsPopup.hidePopup().then(() => {
          this.settingsPopup = null;
        });
      }
      this.gameBoardView.enableButtons();
    });

    this.game.tweens.create(this.gameBoardView.getRoot())
      .from({alpha: 0}, 700, this.game.tweens.easing.Sinusoidal.Out, true);

    this.controller = new Controller(
      this.game,
      {
        board: this.gameBoard,
        boardView: this.gameBoardView,
        onGameOver: async () => await this.maybeGameOver(),
        mobile: window.ontouchstart !== undefined,
      }
    );

    this.root.addChild(this.gameBoardView.getRoot());
    this.loadScore();

    // this.initDebug();
  }

  // public updateState(event: StateEvent) {
  //   switch(this.state) {
  //     case "RegularPlay":
  //       switch(event) {
  //         case "Pause": this.state = "SettingsPopup"; new SettingsPopup(); break;
  //         case "GameOver":
  //           if (!secondChance) {
  //             this.state = "SecondChancePopup"; new SecondChancePopup(); break;
  //           } else {
  //             this.state = "GameOverPopup"; this.trueGameOver(); break;
  //           }
  //       }
  //       break;
  //     case "SettingsPopup":
  //       switch(event) {
  //         case "Close": this.state = "RegularPlay"; break;
  //       }
  //     case "SecondChancePopup":
  //       switch(event) {
  //         case "Close": this.state = "GameOverPopup"; this.trueGameOver(); break;
  //         case "Continue": this.state = "RegularPlay"; this.useSecondChance(); break;
  //       }
  //       break;
  //     case "GameOverPopup":
  //       switch(event) {
  //         case "NewBest": this.state = "Leaderboards"; this.showLeaderboards(); break;
  //         case "Continue": this.state = "RegularPlay"; this.restartGame(); break;
  //       }
  //   }
  // }

  private showSettings() {
    this.settingsPopup = new SettingsPopup({
      tweenMgr: this.game.tweens,
      parent: this.root,
      board: this.gameBoard,
      view: this.gameBoardView,
      onclose: () => {
        this.game.signals.onUnpause.dispatch();
      },
      onrestart: () => {
        this.restartGame();
      }
    });
  }

  private async loadScore(): Promise<void> {
    highscore = (await loadUserData(['highscore'])).highscore || 0;
  }

  private maybeGameOver(dummyScore?: number) {
    const gameState = this.gameBoard.getData().state;

    const score = dummyScore || gameState.score;

    this.game.signals.onPause.dispatch();

    if (gameState.secondChanceAvailable) {
      this.showSecondChancePopup(score);
    } else {
      this.trueGameOver(score);
    }
  }

  private showSecondChancePopup(score: number) {
    const tweenMgr = this.game.tweens;
    new SecondChancePopup({
      tweenMgr, parent: this.root,
      onclose: () => {
        this.trueGameOver(score);
      },
      oncomplete: () => {
        this.useSecondChance();
        this.game.signals.onUnpause.dispatch();
      }
    });
  }

  private useSecondChance() {
    popOutLines(this.gameBoard, 6);
    const gameState = this.gameBoard.getData().state;
    gameState.secondChanceAvailable = false;
    gameState.gameOver = false;
  }

  private trueGameOver(score: number) {
    triggerGameOver(score);

    const overrideScore = true

    if (score > highscore || overrideScore) {
      highscore = score;
      saveUserData({highscore});
      // window._azFeedback.Wrapper.setLeaderboardScore('bShooterChallengeSG', highscore, () => {});
      setPlayerEntry("score", score).then(result => {
        console.log("Result of setting player entry", result);
      });
        new GameOverPopupV2({tweenMgr: this.game.tweens,
          parent: this.root,
          restart: () => {
            new LeaderboardPopupV2({
              parent: this.root,
              tweenMgr: this.game.tweens,
              score,
              onclose: this.restartGame.bind(this)
            });
          },
          score}, true);
    } else {
      // const tweenMgr = this.game.tweens;
    // tweenMgr.create(this.gameBoardView.getRoot())
    //       .to({alpha: 0}, 300, tweenMgr.easing.Sinusoidal.Out, true);
      new GameOverPopupV2({tweenMgr: this.game.tweens,
        parent: this.root,
        restart: this.restartGame.bind(this),
        score}, false);
    }
  }

  private async restartGame() {
    logEvent('game_retry');
    const tweenMgr = this.game.tweens;
    const waitingScreen = new WaitingScreen(this.root);
    waitingScreen.destroy();

    tweenMgr.create(this.gameBoardView.getRoot())
      .to({alpha: 0}, 400, tweenMgr.easing.Sinusoidal.Out, true);
    this.controller.destroy();
    this.game.signals.onPause.removeAll();
    this.game.signals.onUnpause.removeAll();
    this.game.scenes.startScene(ScenesName.Main);
  }

  update(elapsedMs: number) {
    if (this.isPaused) return;

    if (!this.gameStarted && this.gameBoard.getData().state.firstShoot) {
      this.gameStarted = true;
      triggerGameStart();
    }

    if (this.autoUpdate) {
      this.gameBoard.update(elapsedMs);
      this.gameBoardView.update(elapsedMs);
    }
    this.controller.update();
  }

  handleResize([width, height]: [number, number]): void {
    this.bg.handleResize([width, height]);

    const diff = Math.floor(height - appHeight) / 32;
    this.level.shooter.positionY = levelData.shooter.positionY + diff;
    this.level.popOutSpace.pointZonesY = levelData.popOutSpace.pointZonesY + diff;
    this.level.popOutSpace.bouncers.y = levelData.popOutSpace.bouncers.y + diff;
    this.level.grid.deathLineY = levelData.grid.deathLineY + diff;

    this.level.grid.hexLimits.height = levelData.grid.hexLimits.height + Math.floor(diff / 1.5);

    this.gameBoard.resize(this.level);
    this.gameBoardView.resize();
  }
}
