import { swapElements } from "js/framework/utils/array-utils";
import { IShooterData } from "../types";
import { IEvent, createShooterUpdatedEvent } from "../events";

export function swapBubbles(shooter: IShooterData, events: IEvent[]): IEvent[] {
  swapElements(shooter.bubblesQueue, 0, 1);
  events.push(createShooterUpdatedEvent(shooter));
  return events;
}