import * as PIXI from 'pixi.js';

type Require = __WebpackModuleApi.RequireContext;
const atlasesContext: Require = require.context('../../../../static/atlases', true, /\.json$/);
const imagesContext: Require = require.context('../../../../static/images', true);

export class Loader {
  private progress: number = 0;

  public async load(): Promise<void> {
    const loader: PIXI.Loader = PIXI.Loader.shared;

    atlasesContext.keys().forEach((key: string) => {
      loader.add(this.keyToName(key), `atlases/${key.slice(2)}`);
    });

    imagesContext.keys().forEach((key: string) => {
      loader.add(this.keyToName(key), `images/${key.slice(2)}`);
    });

    loader.onError.add(() => window.location.reload());
    const promise: Promise<void> = new Promise((fulfill: () => void): void => loader.onComplete.add(fulfill));
    loader.load();

    return promise;
  }

  private keyToName(key: string): string {
    return (key.split('/').pop() as string).split('.').shift() as string;
  }
}
