type Filter = Partial<{
  root: boolean
  lbHeader: boolean
  headerBg: boolean
  rankLabel: boolean
  playerNameLabel: boolean
  pointsLabel: boolean
  lbEntries: boolean
  entriesBg: boolean
}>;

class LeaderboardLayout {
  public readonly root: PIXI.Container;
  public readonly lbHeader: PIXI.Container;
  public readonly headerBg: PIXI.Sprite;
  public readonly rankLabel: PIXI.Text;
  public readonly playerNameLabel: PIXI.Text;
  public readonly pointsLabel: PIXI.Text;
  public readonly lbEntries: PIXI.Container;
  public readonly entriesBg: PIXI.Sprite;

  constructor(filter?: Filter) {
    populate(this, raw, filter);
  }
}

export { LeaderboardLayout, Filter, raw };