function getSDK() {
  return window._YaSDK;
}

export async function getYandexLeaderboards() {
  const ysdk = getSDK();
  if (!ysdk) return;

  const lb = await ysdk.getLeaderboards()
  console.log("Got lb object", lb)
  return lb
}

export async function setPlayerEntry(leaderboardName: string, value: number) {
  const ysdk = getSDK();
  if (!ysdk) return;

  const available = await ysdk.isAvailableMethod("leaderboards.setLeaderboardScore")
  if (!available) return

  const lb = await ysdk.getLeaderboards()
  const result = lb.setLeaderboardScore(leaderboardName, value);
  console.log("Score setting result", result)
  return result
}

export async function getPlayerEntry(leaderboardName: string) {
  const ysdk = getSDK();
  if (!ysdk) return;

  const available = await ysdk.isAvailableMethod("leaderboards.getLeaderboardPlayerEntry")
  if (!available) {
    console.log ("leaderboards.getLeaderboardPlayerEntry method not available")
    return
  }

  const lb = await ysdk.getLeaderboards()

  const playerEntry = await lb.getLeaderboardPlayerEntry(leaderboardName);

  console.log("Player entry", playerEntry)

  return playerEntry
}

export async function getLeaderboardEntries(leaderboardName: string) {
  const ysdk = getSDK();
  if (!ysdk) return;

  const lb = await ysdk.getLeaderboards();

  const entries: EntriesResponse = await lb.getLeaderboardEntries(leaderboardName, {
    quantityTop: 3,
    includeUser: true,
    quantityAround: 1,
  })

  console.log("LB entries", entries)

  return entries
}

type LeaderboardDescription = {
  appID: string;
  default: boolean;
  description: {
    invert_sort_order: boolean;
    score_format: {
      options: {
        decimal_offset: number;
      };
    };
    type: string;
  };
  name: string;
  title: {
    en: string;
    ru: string;
  };
};

type LeaderboardEntry = {
  score: number;
  extraData: string;
  rank: number;
  player: {
    getAvatarSrc: (size: string) => string;
    getAvatarSrcSet: (size: string) => string;
    lang: string;
    publicName: string;
    scopePermissions: {
      avatar: string;
      public_name: string;
    };
    uniqueID: string;
  };
  formattedScore: string;
};

type EntriesResponse = {
  leaderboard: LeaderboardDescription;
  ranges: [
    {
      start: number;
      size: number;
    }
  ];
  userRank: number;
  entries: LeaderboardEntry[];
};
