import * as PIXI from 'pixi.js';
import { deg2rad, IPoint } from './utils/math-utils';
import { game as app } from 'js/main';

const layoutsContext = require.context('js/layouts', true);

export const populate = (layout, raw, filter = {}): void => {
  const map = {};

  for (const model of raw.list) {
    if (model.className) {
      layout[model.className] = layout[model.className] || [];
    }

    if (filter[model.name] || filter[model.className] || (model.parent && !map[model.parent])) continue;

    let object;
    let view;

    if (model.type === ModelType.Layout) {
      const data = createInnerLayout(model);

      if (!data) continue;

      view = data.root;
      object = data.layout;
    } else {
      view = createView(model);
      object = view;
    }

    map[model.id] = view;
    map[model.parent] && map[model.parent].addChild(view);

    if (model.name) {
      layout[model.name] = object;
    }

    if (model.className) {
      layout[model.className].push(object);
    }
  }

  const rootModel = raw.list[0];
  const rootView = layout[rootModel.name];
  setRelativePosition(rootView, raw.x, raw.y, rootModel.x, rootModel.y);

  if (raw.handleResize) {
    addResizeHandler(rootView, raw.x, raw.y, rootModel.x, rootModel.y);
  }
};

const setRelativePosition = (view, percentX, percentY, x, y): void => {
  view.x = x + (percentX * app.screen.width) / 100;
  view.y = y + (percentY * app.screen.height) / 100;
};

const addResizeHandler = (view, percentX, percentY, x, y): void => {
  const handleResize = (): void => {
    if (view._destroyed) {
      unsubscribe();
      return;
    }

    setRelativePosition(view, percentX, percentY, x, y);
  };

  const unsubscribe = () => app.signals.onResize.remove(binding);
  const binding = app.signals.onResize.add(handleResize);
  view.onDestroyed.addOnce(unsubscribe);
};

